body {
  margin: 0;
  overflow: hidden;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

h1 {
  margin: 0;
}

* {
  /* font-family: "Anek Latin", sans-serif; */
}

*::selection {
  background-color: aliceblue;
}

/* Home page CSS */
#home-page-header {
  width: 100%;
  height: 10vh;
  overflow: hidden;
  position: relative;
  /* border-bottom: 1px solid #d5dde6 ; */
  font-family: "Poppins", "Helvetica Neue", Helvetica, Arial, sans-serif;
  /* font-family: "Anek Latin", sans-serif; */
}

#home-page-header-title {
  position: absolute;
  /* height: 100%;
  width: 50%;
  right: 0; */
  top: 10%;
  left: 1%;
  /* padding-top: 10px; 
  padding-left: 10px; */
  font-size: 50px;
  font-weight: 800;
  color: #414042;

  left: 4%;
  color: #1b1b1c;
}

#home-page-body {
  /* background-image: url("https://s3-media0.fl.yelpcdn.com/educatorphoto/oAiaOrUctS_W5awpIL6lhA/o.jpg"); */
  background-size: cover;
  width: 100%;
  /* height: 90%; */
  display: flex;
  /* justify-content: center; */
  align-items: center;

  flex-direction: column;
  justify-content: space-around;
  opacity: 0.9;

  height: 85%;
}

#home-page-form-title {
  /* font-family: "Anek Latin", sans-serif; */
  /* color: #414042; */
  font-weight: 500;
  font-size: 20px;
  padding: 5px;
}

#input-address-one,
#input-address-two,
#select-activity {
  width: 100%;
}

#home-page-footer {
  width: 100vw;
  /* height: 20vh; */
  /* height: 10vh; */
  display: flex;
  flex-direction: column;
  /* justify-content: space-evenly;
  align-items: center; */
  border-top: 1px solid #d5dde6;
  /* font-family: "Anek Latin", sans-serif; */
  height: 15%;
}

#home-page-body-wrapper {
  width: 100%;
  height: 90%;
  background: rgba(0, 0, 0, 0)
    linear-gradient(to bottom, rgba(0, 0, 0, 0) 0px, rgba(0, 0, 0, 0.6) 100%)
    repeat 0 0;
}

#home-page-form-button {
  width: 100%;
}

#footer-slogan {
  width: 100%;
  line-height: 45px;
  font-size: 20px;
  font-weight: 800;
  color: #27252b;
  font-family: "Anek Latin", sans-serif;

  vertical-align: middle;
  text-align: center;
  /* font-family: 'Dancing Script', cursive; */
  /* height: 100%; */
}

#slogan {
  color: white;
  font-size: 20px;
  font-family: "Dancing Script", cursive;
  /* font-family: 'Open Sans','Helvetica Neue',Helvetica,Arial,sans-serif; */
}

#footer-contact-links {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}

#github-link,
#personal-page-link {
  color: inherit;
  font-size: 15px;
  text-decoration: none;
}

#home-page-container {
  /* color: white; */
  /* font-family: 'Poppins','Helvetica Neue',Helvetica,Arial,sans-serif; */
  width: 100vw;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}

#hope-page-form-outline {
  padding: 40px;
}

#hope-page-background-image {
  background-size: cover;
  position: absolute;
  inset: 0;
  width: 150%;
  height: 200%;
  margin-bottom: 50%;
  margin-left: 50%;
  /* margin-bottom: 20%; */
  transform: translate(-50%, -30%);
  z-index: -1;
}

#transportation-label {
  /* font-family: "Anek Latin", sans-serif; */
  color: #414042;
  font-size: 16px;
  margin-bottom: 10px;
  margin-top: 10px;
  text-align: left;
}

#home-page-form-button {
  width: 100%;
}
