/* Google place API autocomplete */
.pac-container {
  font-family: "Roboto", Arial, sans-serif;
  text-align: center;
  width: 100%;
}

.pac-matched {
  font-size: 14px;
  font-weight: 700;
}

.pac-item-query {
  font-size: 14px;
  font-weight: 400;
}

.pac-item {
  font-size: 14px;
  height: 50px;
  line-height: 50px;
  font-weight: 400;
}

/* !Theres a problem showing the icon */
.pac-icon,
.pac-item-selected > .pac-icon {
  /* background-image: url("../public/map-pin-solid.svg"); */
  /* background: url("../public/map-pin-solid.svg");
  background-repeat: no-repeat;
  background-size: auto;
  margin: 15px 10px 0 10px; */
  margin: 15px 10px 0 10px;
}

.pac-item:hover {
  background-color: #eff5fb;
}

.pac-container:after {
  background-image: none !important;
  height: 0px;
}

/* Meeting Locations Page */

#meeting-locations-header {
  font-size: 5vh;
  height: 100%;
}

#meeting-locations-header-title {
  /* position: absolute;
  top: 1%;
  left: 2%; */

  margin: 10px 40px 0 20px;
  cursor: pointer;
  width: fit-content;
  font-family: "Poppins", "Helvetica Neue", Helvetica, Arial, sans-serif;
  font-weight: 800;
  color: #1b1b1c;
}

/* Meeting Locations List */
.business-title,
.business-rating,
.business-categories,
.business-open,
.business-review {
  margin-bottom: 7px;
}

.business-title {
  font-size: 17px;
  font-weight: 700;
}

.business-content,
.business-information {
  font-family: "Open Sans", sans-serif;
}

.meeting-location-list-title {
  font-family: "Poppins", "Helvetica Neue", Helvetica, Arial, sans-serif;
  font-size: 30px;
  font-weight: 600;
  margin: 20px 10px 40px 10px;
}

.business-rating {
  font-weight: 500;
  display: flex;
  align-items: center;
}

.business-rating-number-reviews {
  line-height: 1;
}

.business-review {
  /* margin: 10px 15px 0px 15px; */
  margin-top: 10px;
  color: grey;
}

#meeting-locations-business-information-back-button {
  position: absolute;
  top: 5px;
  left: 5px;
  background-color: #ff0000;
  z-index: 1;
  border: 2px white solid;
}

#meeting-locations-business-information-image {
  box-shadow: inset 10px 10px 50px #fff;
}

#business-information-header {
  position: absolute;
  left: 5%;
  bottom: 5%;
  z-index: 1;
  color: white;
  /* text-shadow: 2px 2px 20px rgba(0, 0, 0, 1); */
}

#business-information-title-in-image {
  font-weight: 700;
  color: white;
  text-shadow: 2px 2px 20px rgba(0, 0, 0, 1);
}

.business-information-title-in-image-anchor {
  color: inherit;
  text-decoration: underline 0.15em rgba(0, 0, 0, 0);
  transition: text-decoration-color 300ms;
}

.business-information-title-in-image-anchor:hover {
  text-decoration-color: white;
}

#business-information-rating-text {
  color: white;
  line-height: 30px;
  font-weight: 700;
  font-size: 14px;
  text-shadow: 2px 2px 21px rgba(0, 0, 0, 1);
}

#meeting-locations-business-information-more-image-button {
  position: absolute;
  bottom: 5px;
  right: 5px;
  background-color: white;
  z-index: 1;
}

#business-information-title {
  font-weight: 700;
  font-size: 30px;
  margin-top: 5%;
  margin-left: 5%;
}

#business-information-hours,
#business-information-address-header {
  font-weight: 800;
}

#business-information-address > div {
  text-align: center;
}

.business-information-main-address {
  font-weight: 500;
  font-size: 0.9rem;
}

.business-information-hours-day {
  font-size: 1rem;
  font-weight: 600;
}

.business-information-hours-day-hours {
  font-size: 0.75rem;
  font-weight: 500;
}

#business-information-no-hours {
  font-size: 0.9rem;
  font-weight: 500;
}

.business-information-postal-information {
  font-size: 0.8rem;
  color: grey;
}

#business-information-reviews-header {
  font-size: 1.5rem;
  font-weight: 600;
  text-align: center;
}

.business-information-review-url,
.business-information-reviewer-profile-url {
  text-decoration: underline;
  color: inherit;
}

#business-information-reviews-no-reviews {
  font-size: 1rem;
  font-weight: 600;
  text-align: center;
  color: grey;
}

/* Google Maps */
.marker-label {
  /* font-family: 'Roboto', sans-serif; */
  /* font-weight: 300; */
  font: 400 11px "Roboto", Arial, sans-serif;
}

#meeting-locations-header-form-button {
  margin-right: 40px;
  width: 100%;
}

.marker-title-name {
  color: #333;
  font-weight: 500;
  font-size: 14px;
}

.marker-address {
  font-size: 13px;
}

.marker-links-container {
  display: flex;
  flex-direction: column;
}

.marker-links {
  font-size: 13px;
  color: #1a73e8;
  text-decoration: none;
}

.marker-links:hover {
  text-decoration: underline;
}

/* Page Not Found CSS */
#page-not-found-heading,
#page-not-found-message,
#page-not-found-pathname,
#page-not-found-button {
  /* font-family: "Anek Latin", sans-serif; */
}

#page-not-found-heading {
  margin: 0;
  font-size: 50px;
  font-weight: 800;
}

#page-not-found-message {
  font-size: 20px;
  font-weight: 400;
}

#page-not-found-body {
  text-align: center;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  display: flex;
}

#page-not-found-button {
  font-size: 15px;
  margin-top: 5px;
}

#page-not-found-link {
  color: inherit;
  text-decoration: none;
}

#page-not-found-image {
  height: 40vh;
}
